.hystmodal {
	&__window {
		max-width: 676px;
		width: 100%;
		border-radius: 35px;
		overflow: hidden;
	}

	&::before {
		opacity: 1;
		background-color: rgba($color-main, 0.8);
	}

	&__close {
		position: absolute;
		top: 20px;
		right: 20px;
		display: flex;
		align-items: center;
		justify-content: center;
		width: 30px;
		height: 30px;
		padding: 0;
		cursor: pointer;
		background-image: unset;
		background-size: unset;

		&:focus {
			outline: none;
			outline-offset: unset;
		}

		&:hover svg {
			opacity: 0.7;
		}

		svg {
			transition: 0.3s ease;
		}
	}
}
